.green-dot {
  position: relative; }
  .green-dot::after, .green-dot::before {
    content: '';
    display: inline-block;
    position: absolute;
    border-radius: 100%;
    left: -15px;
    top: 3px; }
  .green-dot::after {
    width: 10px;
    height: 10px;
    background: rgba(86, 203, 100, 0.4); }
  .green-dot::before {
    top: 5px;
    left: -13px;
    width: 6px;
    height: 6px;
    background: #56cb64; }

.button {
  background: linear-gradient(270deg, #FFCC00 0%, #FFA300 100%);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  text-decoration: none;
  color: white;
  min-height: 70px;
  padding: 20px;
  border-radius: 70px;
  max-width: 412px;
  font-weight: 700;
  font-size: 18px;
  line-height: 120%;
  display: flex;
  align-items: center;
  justify-content: center; }
  .button span {
    padding-left: 20px;
    position: relative;
    z-index: 2; }
    .button span::before {
      content: '';
      display: inline-block;
      position: absolute;
      width: 10px;
      height: 10px;
      border-top: 3px solid white;
      border-right: 3px solid white;
      transform: rotate(45deg);
      left: -8px;
      top: 3px; }
  .button:hover, .button:focus {
    color: black;
    transition: all 0.4s;
    background: linear-gradient(270deg, #FFA300 0%, #FFCC00 100%); }
    .button:hover span::before, .button:focus span::before {
      transition: all 0.4s;
      border-top: 3px solid black;
      border-right: 3px solid black; }

.number {
  display: flex;
  align-items: center;
