.button {
  background: linear-gradient(270deg, #FFCC00 0%, #FFA300 100%);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  text-decoration: none;
  color: $white;
  min-height: 70px;
  padding: 20px;
  border-radius: 70px;
  max-width: 412px;
  font-weight: 700;
  font-size: 18px;
  line-height: 120%;

  @include flex(center, center);

  span {
    padding-left: 20px;
    position: relative;
    z-index: 2;

    &::before {
      content: '';
      display: inline-block;
      position: absolute;
      width: 10px;
      height: 10px;
      border-top: 3px solid $white;
      border-right: 3px solid $white;
      transform: rotate(45deg);
      left: -8px;
      top: 3px;
    }
  }

  &:hover,
  &:focus {
    color: $black;
    transition: all 0.4s;
    background: linear-gradient(270deg, #FFA300 0%, #FFCC00 100%);

    span {
      &::before {
        transition: all 0.4s;
        border-top: 3px solid $black;
        border-right: 3px solid $black;
      }
    }
  }
}