.swiper {
  position: relative;
  overflow-x: hidden;
}

.swiper-button-prev,
.swiper-button-next {
  @include flex(center, center);

  cursor: pointer;
  position: absolute;
  top: 53%;
  width: 70px;
  height: 70px;
  background: #FFFFFF;
  box-shadow: 0px 6px 30px rgba(0, 0, 0, 0.05);
  border-radius: 300px;
  border: 1px solid $gray;

  &::before {
    content: '';
    display: inline-block;
    width: 5px;
    height: 5px;
    border: 3px solid $black;
    transform: rotate(45deg);
  }

  &:hover,
  &:focus {
    background-color: $yellow;
    transition: all 0.4s;
  }
}

.swiper-button-prev {
  left: -14px;

  &::before {
    border-top: none;
    border-right: none;
  }
}

.swiper-button-next {
  right: -14px;

  &::before {
    border-bottom: none;
    border-left: none;
  }
}

.swiper-slide {
  transition: all 0.4s;
}
/*
.swiper-slide-prev {
  opacity: 0;
}
*/

.swiper-slide-active,
.swiper-slide-next {
  opacity: 1;
}

.swiper-pagination {
  @include flex(center, center);

  width: 100%;
  min-height: 26px;
}

.swiper-pagination-bullet {
  width: 6px;
  height: 6px;
  background-color: $black;
  border-radius: 100%;
  transition: all 0.2s;
  margin: 0 20px;
  display: inline-block;
  cursor: pointer;
}

.swiper-pagination-bullet-active {
  width: 9px;
  height: 9px;
  border: 3px solid $black;
  background-color: $white;
}

.swiper-button-disabled {
  pointer-events: none;
}
